
.player-name {
  font-size: 16px;
}
.info {
  font-family: "courier-prime", Courier, sans-serif;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: -0.01em;
}
.policy {
  width: 200px;
  height: 200px;
  background-size: contain;
  background-position: center;
  -webkit-mask-size: contain;
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
}
.policy.liberal {
  -webkit-transform: rotateZ(-90deg);
          transform: rotateZ(-90deg);
  -webkit-mask-image: url('../../assets/misc/dove.svg');
  background-color: rgba(0, 145, 179, 0.75);
}
.policy.fascist {
  -webkit-transform: rotateZ(90deg);
          transform: rotateZ(90deg);
  -webkit-mask-image: url('../../assets/misc/skull.svg');
  background-color: rgba(214, 13, 0, 0.75);
}



